import PressPopup from '../press-popup-plus/press-popup-plus.vue';
import PressIconPlus from '../press-icon-plus/press-icon-plus.vue';

import utils from '../common/utils/utils';
import { vModelMixin } from '../common/vue3/adapter';

import { transition } from '../mixins/transition';
import { ClickOutsideMixin } from '../mixins/click-outside';


export default {
  name: 'PressPopoverPlus',
  options: {
    styleIsolation: 'shared',
  },
  components: {
    PressPopup,
    PressIconPlus,
  },
  mixins: [
    vModelMixin,

    transition(false),

    ClickOutsideMixin({
      event: 'touchstart',
      method: 'onClickOutside',
    }),

  ],
  props: {
    // value: {
    //   type: Boolean,
    //   default: false,
    // },
    overlay: {
      type: Boolean,
      default: false,
    },
    trigger: {
      type: String,
      default: '',
    },
    offset: {
      type: Array,
      default: () => [0, 8],
    },
    theme: {
      type: String,
      default: 'light',
    },
    actions: {
      type: Array,
      default: () => [],
    },
    placement: {
      type: String,
      default: 'bottom',
    },
    getContainer: {
      type: [String, Function],
      default: 'body',
    },
    closeOnClickAction: {
      type: Boolean,
      default: true,
    },
    zIndex: {
      type: Number,
      default: 1,
    },
    customStyle: {
      type: String,
      default: '',
    },
    duration: {
      type: Number,
      default: 200,
    },
  },
  emits: [
    'close',
    'closed',
    'input',
    'open',
    'opened',
    'select',
    'touchstart',
  ],
  data() {
    return {
      utils,
    };
  },
  computed: {
    popoverStyle() {
      const { zIndex } = this;
      return `z-index: ${zIndex}`;
    },
    wrapperStyle() {
      const { customStyle } = this;
      return customStyle;
    },
    popupClass() {
      const { theme, placement } = this;
      return utils.bem2('popover', [theme, placement]);
    },
    popupCustomClass() {
      let result = '';



      return result;
    },
  },
  mounted() {
  },
  methods: {
    setData(data) {
      Object.keys(data).forEach((key) => {
        this[key] = data[key];
      });
    },
    // onToggle(value) {
    //   // this.$emit('input', value);
    // },

    onClickWrapper() {
      if (this.trigger === 'click') {
        // this.onToggle(!this.value);
        this.emitModelValue(!this.realModelValue);
      }
    },

    onTouchstart(event) {
      event.stopPropagation();
      this.$emit('touchstart', event);
    },

    onClickAction(action, index) {
      if (action.disabled) {
        return;
      }

      this.$emit('select', action, index);

      if (this.closeOnClickAction) {
        this.$emit('input', false);
      }
    },

    onClickOutside() {
      this.$emit('input', false);
    },

    onOpen() {
      this.$emit('open');
    },

    /* istanbul ignore next */
    onOpened() {
      this.$emit('opened');
    },

    onClose() {
      this.$emit('close');
    },

    /* istanbul ignore next */
    onClosed() {
      this.$emit('closed');
    },
  },
};

